<template>
  <div id="app">
    <Home @openShop="openShop" @showList="showList" />
    <Modal @closeShop="closeShop" v-if="shop" />
    <List v-if="listVisable" @hideList="hideList" />
  </div>
</template>

<script>
import Home from '@/components/Home.vue';
import Modal from '@/components/Modal.vue';
import List from '@/components/List.vue';

export default {
  name: 'App',
  components: {
    Home,
    Modal,
    List
  },
  data() {
    return {
      shop: false,
      listVisable: false
    }
  },
  methods: {
    openShop() {
      this.shop = true;
    },
    closeShop() {
      this.shop = false;
    },
    showList() {
      this.listVisable = true;
    },
    hideList() {
      this.listVisable = false;
    }
  }
}
</script>

